body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media all and (display-mode: standalone) {
    .App-bar {
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        height: 50px;
        background-color: yellow;
    }
    .App-bar::before {
        content: 'App launched as installed application';
    }
}

.App {
    text-align: center;
}

.App-header {
    background-color: #2254b9;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}
body{
    background: #f8f8f8;
}
p, li{
    color: #333!important;
}
#two li{
    color: #f3f3f3!important;
}
section{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    padding-top: 130px;
}
ul{
    text-align: left;
}
section#one img{
    height:450px;
}
section#one h4{
    color: #444!important;
    margin-bottom: 0;
    margin-top: -30px;
}
section#one h1{
    margin-bottom: 20px;
}
section#two h1, li{
    color: #f3f3f3;
    list-style-type: none;
}
section li, a, p{
    font-size: 2rem;
}
p{
    text-align: left;
}
.slide{
    background-image: url("/bg.jpg");
}
.slide:nth-child(1){
    background: #f8f8f8;
}
.slide:nth-child(2){
    background-image: url("/bg2.jpg");
}
section h1{
    color: #0286ce;
    font-size: 4rem;
}
section h4{
    font-size: 2rem;
}
.show-index.is-dot .dot{
    background-color: #030303!important;
}
.show-index.is-dot{
    color: #fff;
    position: absolute;
    bottom: 0;
    z-index: 100;
    font-size: 28px;
    left: 0!important;
    font-family: 'Slabo 27px', serif;
    width: 100%!important;
    display: flex!important;
    justify-content: center!important;
}
.emoji{
    width: 4rem;;
}
.center{
    padding-top: 0;
    justify-content: center;
}
.ta-center{
    text-align: center
}
ul{
    padding: 0;
    margin: 0 150px;
}
li{
    margin-top: 20px;
}
h2{
    font-size: 3rem;
}
section#four p{
    margin: 0 150px;
}
